.bothCounters {
    display: flex;
    justify-content: space-around;
}

.container {
    width: 400px;
    height: 400px;
    border: 2px solid darkblue;
    border-radius: 10px;
    margin: 100px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    /*justify-content: center;*/
    padding: 20px;
}

.buttons {
    font-size: 70px;
    background-color: aquamarine;
    margin: 0 10px;
    border-radius: 10px;
    font-weight: bold;
    border: 3px solid white;
}

.buttons:disabled {
    border: none;
}

.counter, .buttonBlock {
    display: flex;
    background-color: aquamarine;
    height: 180px;
    width: 100%;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
    border-radius: 5px;
    font-size: 100px;
    font-weight: bold;
}

.limit {
    color: red;
}
.message,.errorClass {
    font-size: 26px;
    color:blue;
}
/*//----------------------------------------------------------------------*/
.buttonBlock {
    background-color: darkslateblue;
    margin-top: 10px;
}

